import { useState, useEffect } from 'react';
import * as emailjs from 'emailjs-com';
import { Field, Control } from 'rbx';
import { motion, useAnimation } from "framer-motion";
import { useInView } from 'react-intersection-observer';

export default function Contact() {
    const controls = useAnimation();
    const { ref, inView } = useInView();
    const boxVariants = {
        hidden: { x: -100 },
        visible: { x: 0 }
    }
    const inputVariants = {
        hidden: { y: 100 },
        visible: { y: 0 }
    }
    const letterHover = {
        scale: 1.3,
        color: 'rgb(64,224,208)'
    }
    const [contactForm, setContactForm] = useState({name:'', email:'', subject:'', message:''})
    const [sentMessage, setSentMessage] = useState(false)
    const resetForm = ()=> setContactForm({ name:'', email:'', subject:'',  message:'' }) 
    const handleChange = ({ name, value })=> setContactForm(prev=> ({...prev, [name]: value}));
    const handleSubmit = e =>{
        e.preventDefault();
        const templateParams = {
          from_name: contactForm.name,
          from_email: contactForm.email,
          from_subject: contactForm.subject,
          message_html: contactForm.message,
        };
        emailjs.send( 'service_5lhgvmk', 'template_yuu79ag', templateParams, 'user_xyFHlx2eg6bbAezEi6Dbu' )
        resetForm();
        setSentMessage(true);
    };
    
    useEffect(()=>{
        if (inView) controls.start('visible');
        if (!inView) controls.start('hidden');
    }, [controls, inView, contactForm])
    return (
        <div className='container-fluid py-4' style={{ backgroundColor: '#1D1D1D', color: 'white', position: 'relative' }}>
            <div className='container'>
                <motion.div 
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants}
                    className='container d-inline-flex' id='contact'
                > 
                    {`Contact`.split('').map((e,i)=> /[\s]/.test(e) ? <span>&nbsp;</span> : 
                    <motion.h1 key={i} whileHover={letterHover}>{e}</motion.h1> )}
                </motion.div>
            </div>
            <div className='container d-flex flex-wrap align-items-center' >
                <form onSubmit={handleSubmit} className='mb-4 mr-4'>
                    <Field>
                        <Control>
                            <motion.input name="name" type="text" placeholder="Name" value={contactForm.name} onChange={e=>handleChange(e.target)} className='form-control form m-2' ref={ref} initial='hidden' animate={controls} variants={inputVariants} whileHover={{ scale: 1.05 }} />
                        </Control>
                    </Field>
                    <Field>
                        <Control>
                            <motion.input name="email" type="email" placeholder="Email" value={contactForm.email} onChange={e=>handleChange(e.target)} className='form-control form m-2' ref={ref} initial='hidden' animate={controls} variants={inputVariants} whileHover={{ scale: 1.05 }} />
                        </Control>
                    </Field>
                    <Field>
                        <Control>
                            <motion.input name="subject" type="text" placeholder="Subject" value={contactForm.subject} onChange={e=>handleChange(e.target)} className='form-control form m-2' ref={ref} initial='hidden' animate={controls} variants={inputVariants} whileHover={{ scale: 1.05 }} />
                        </Control>
                    </Field>
                    <Field>
                        <Control>
                            <motion.textarea name="message" placeholder="Message" value={contactForm.message} cols='50' rows='5' onChange={e=>handleChange(e.target)} className='form-control form m-2' ref={ref} initial='hidden' animate={controls} variants={inputVariants} 
                            whileHover={{ scale: 1.05 }} />
                        </Control>
                    </Field>
                    {sentMessage && (<div className='container'>Message Sent</div>)}
                    <Field kind="group" className='container justify-content-end d-flex'>
                        <Control> 
                            <motion.button className='btn btn-outline-secondary' ref={ref} initial='hidden' animate={controls} variants={inputVariants} whileHover={{ type: 'spring', stiffness: 900, scale: 1.3, boxShadow: "0px 0px 8px #FFF" }} >Send</motion.button> 
                        </Control>
                    </Field>
                </form>
                <motion.div className='container col' style={{minWidth: '300px'}} ref={ref} initial='hidden' animate={controls} variants={inputVariants}>
                <img src={'https://myprojectsfromportfolio.s3.ca-central-1.amazonaws.com/map.png'} alt="map" width='500px' className='img-fluid' />
                </motion.div>
            </div>
            
        </div>
    )
}