import { useEffect } from 'react'
import { motion, useAnimation } from "framer-motion";
import { useInView } from 'react-intersection-observer';

export default function Intro() {
    const controls = useAnimation();
    const { ref, inView } = useInView();
    useEffect(()=>{
        if (inView) controls.start('visible');
        if (!inView) controls.start('hidden');
    }, [controls, inView])
    const boxVariants = {
        hidden: { scale: 0 },
        visible: { 
            scale: 1,
            transition: {
                delay: 0.5
            }
        }
    }
    const boxVariants2 = {
        hidden: { scale: 0 },
        visible: { 
            scale: 1,
            transition: {
                delay: 0.75
            }
        }
    }
    const boxVariants3 = {
        hidden: { scale: 0 },
        visible: { 
            scale: 1,
            transition: {
                delay: 1.0
            }
        }
    }
    const letterHover = {
        scale: 1.3,
        color: 'rgb(64,224,208)'
    }
    return (
        <div className='container align-items-center row' id='intro' >
            <div className='container d-flex flex-column align-items-center'>
                <motion.div
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants}
                    className='container d-inline-flex justify-content-center col-sm'
                >
                    {`Hi, I'm Cheng,`.split('').map((e,i)=> /[\s]/.test(e) ? <span key={i}>&nbsp;</span> : 
                    <motion.h1 key={'a'+i} whileHover={letterHover}>{e}</motion.h1> )}
                </motion.div>
                <motion.div
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants2}
                    className='container d-inline-flex justify-content-center col-sm'
                >
                    {`full stack web developer`.split('').map((e,i)=> /[\s]/.test(e) ? <span key={i}>&nbsp;</span> : 
                    <motion.h3 key={'b'+i} whileHover={letterHover}>{e}</motion.h3> )}
                </motion.div>
                <motion.div
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants3}
                    className='container d-inline-flex justify-content-center col-sm' 
                >
                    {`from Vancouver, BC`.split('').map((e,i)=> /[\s]/.test(e) ? <span key={i}>&nbsp;</span> : 
                    <motion.h3 key={'c'+i} whileHover={letterHover}>{e}</motion.h3> )}
                </motion.div>
            </div>
        </div>
    )
}
