import { useEffect } from 'react'
import { motion, useAnimation } from "framer-motion";
import { useInView } from 'react-intersection-observer';

export default function About() {
    const controls = useAnimation();
    const { ref, inView } = useInView();
    useEffect(()=>{
        if (inView) controls.start('visible');
        if (!inView) controls.start('hidden');
    }, [controls, inView])
    const boxVariants = {
        hidden: { x: -100 },
        visible: { x: 0 }
    }
    const textVariants = {
        hidden: { x: 100 },
        visible: { x: 0 }
    }
    const letterHover = {
        scale: 1.3,
        color: 'rgb(64,224,208)'
    }
    const summary = `I build things for the web.
    I am a software developer who recently graduated from CodeCore. 
    I'm passionate and motivated to learn and create something that is amazing. 
    I'm open to different ways of thinking and willing to try something new.
    I am a full-stack web developer and UI/UX javascript specialist
    based in Vancouver, BC. I specialize in designing websites and application.
    Come check out my latest projects and live demos of the work I've done.`
    return (
        <div className='container-fluid d-flex flex-wrap justify-content-center py-4' style={{ backgroundColor: '#1D1D1D', color: 'white', position: 'relative'}}>
            <div className='container'>
                <motion.div
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants}
                    className='container d-inline-flex' id='about'
                >
                    {`About`.split('').map((e,i)=> /[\s]/.test(e) ? <span>&nbsp;</span> : 
                    <motion.h1 key={i} whileHover={letterHover}>{e}</motion.h1> )}
                </motion.div>
                <motion.p className='container' ref={ref} initial='hidden' animate={controls} variants={textVariants}> {summary} </motion.p>
            </div>
            
        </div>
    )
}