import { useEffect } from 'react';
import { motion, useAnimation } from "framer-motion";
import { useInView } from 'react-intersection-observer';
import projects from '../ProjectData';
import Aos from "aos"
import "aos/dist/aos.css";

export default function Project() {
    const controls = useAnimation();
    const { ref, inView } = useInView();
    useEffect(()=>{
        if (inView) controls.start('visible');
        if (!inView) controls.start('hidden');
        Aos.init({duration: 500})
    }, [controls, inView])
    const boxVariants = {
        hidden: { x: -100 },
        visible: { x: 0 }
    }
    const letterHover = {
        scale: 1.3,
        color: 'rgb(64,224,208)'
    }
    const cardHover = {
        scale: 1.2,
        color: 'rgb(64,224,208)'
    }
    return (
        <div className='container-fluid' style={{ backgroundColor: '#1D1D1D', position: 'relative'}}>
            <div className='container'>
                <motion.div
                    ref={ref} initial='hidden' animate={controls} variants={boxVariants}
                    className='container d-inline-flex' id='project'
                >
                    {`Projects`.split('').map((e,i)=> /[\s]/.test(e) ? <span>&nbsp;</span> : 
                    <motion.h1 key={i} whileHover={letterHover}>{e}</motion.h1> )}
                </motion.div>
                <div className='container-fluid d-flex flex-wrap p-0'>
                    {projects.map((e,i)=>{
                        return (
                        <div key={i} className="card p-2 m-3" style={{ width: '20rem'}}  data-aos="flip-up" >
                            <motion.a href={e.url} target='_blank' rel="noreferrer"  whileHover={cardHover}><img className="card-img-top" src={'https://myprojectsfromportfolio.s3.ca-central-1.amazonaws.com/' + e.img} alt={e.title} /></motion.a>
                            <div className="card-body">
                                <h5 className="card-title">{e.title}</h5>
                                <p className="card-text">{e.technology}</p>
                            </div>
                        </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}