import { motion } from "framer-motion";
import { faGithub, faLinkedin } from "@fortawesome/free-brands-svg-icons";
// import { faAt, faGlobeAmericas } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function Navbar() {
    
  return(
    <motion.nav className="navbar navbar-dark container-fluid" 
        initial={{ y: '-100vh' }}
        animate={{ y: 0 }}
        transition={{ delay: 1.5 }}
        style={{ position: 'fixed'}}
    >   
        <div/>
        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapsibleNavbar">
            <span className="navbar-toggler-icon"></span>
            <div className="collapse navbar-collapse justify-content-between bg-dark" id="collapsibleNavbar" >
            <ul className="navbar-nav p-4" >
                <li className="nav-item">
                    <a href='#intro' className="nav-link">Home</a>
                </li>
                <li className="nav-item">
                    <a href='#about' className="nav-link">About</a>
                </li>
                <li className="nav-item">
                    <a href='#project' className="nav-link">Projects</a>
                </li>
                <li className="nav-item">
                    <a href='#contact' className="nav-link">Contact</a>
                </li>
                <li className="nav-item">
                    <a href="http://github.com/chengpham" target="_blank" rel="noreferrer" className="nav-link">
                        <FontAwesomeIcon icon={faGithub}/>
                    </a> 
                </li>
                <li className="nav-item">
                    <a href="http://linkedin.com/in/chengpham" target="_blank" rel="noreferrer" className="nav-link">
                        <FontAwesomeIcon icon={faLinkedin}/>
                    </a> 
                </li>
            </ul>
        </div>
        </button>
        
    </motion.nav>
  )
}


